.textContainer
{
   
    margin: auto;
    margin-top: 30px;
    flex-direction: column;
    height: 400px;
    width: 900px;
    background-color: burlywood;
    border: solid black 3px;
    border-radius: 20px;
}


.topButtons
{
    display: flex;
    padding: 5px;
    justify-content: space-evenly;
    margin-top: 10px;
}




.bottom
{
    display: flex;
    height: 300px;

}

.sidebuttons
{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    margin-left: 4px;
}


.textBar
{
    margin-left: 10px;
    margin-top: 20px;
}

.textBox
{

 height: 250px;
 background-color: #e2e2e2;;
 width: 750px;
 border: solid 3px black;
 border-radius: 5px;
    
} 

button
{
    height: 50px;
    width: 90px;
    border: solid black 2px ;
    border-radius: 5px;
    background-color: #9898d4;
    color: #000;
    font-weight: 500;
}

.small
{
    font-size: 12px;
}

